import React, { useState, useEffect } from 'react';
import axios from 'axios';
import ProductCarousel from '../components/productCarousel';
import SubcatCarousel from '../components/subcatCarousel';
import Navbar from '../components/navbar';
import Arrow from '../components/icons/Back.png'
import { addProductToBasket } from '../utils/Service'
import { Link, useHistory } from 'react-router-dom';
import "./Edit.css"

const Edit = ({handleEditPress}) => {
  const history = useHistory();

  const [fetched, setFetched] = useState(false);
  const [categories, setCategories] = useState([]);
  const [balloons, setBalloons] = useState([]);
  const [cart, updateCart] = useState({});
  const [subtotal, setSubtotal] = useState(0.00);
  // const [cart, updateCart] = useState(!!localStorage.CART && JSON.parse(localStorage.CART) || {});
  // const [subtotal, setSubtotal] = useState(!!localStorage.CART && JSON.parse(localStorage.CART).product_sub_total || 0.00);

  const [prevCategory, setPrevCategory] = useState(0);
  const [activeCategory, setActiveCategory] = useState(0);

  const [prevSubcategory, setPrevSubcategory] = useState(0);
  const [activeSubcategory, setActiveSubcategory] = useState(null);

  const [prevBalloon, setPrevBalloon] = useState(0);
  const [activeBalloon, setActiveBalloon] = useState(null);

  
  useEffect(async () => {
    fetchCategories();
    updateCart(!!localStorage.CART && JSON.parse(localStorage.CART) || {})
    setSubtotal(!!localStorage.CART && JSON.parse(localStorage.CART).product_sub_total || 0.00)
  }, []);

  useEffect(() => {
    if (fetched) {
      const currActive = document.getElementById(`nav${prevCategory}`);
      currActive.style.opacity = ".4";

      const newActive = document.getElementById(`nav${activeCategory}`)
      newActive.style.opacity = "1";

      // adjustment for hardcoded cats in navbar
      if (activeCategory > 0) {
        setActiveBalloon(0);
        fetchBalloons(categories[activeCategory].id);
        setPrevCategory(activeCategory);
        return;
      }

      setActiveSubcategory(0);
      setActiveBalloon(0);
      fetchBalloons(categories[activeCategory].categories[0].id);
      setPrevCategory(activeCategory)
    }
  }, [activeCategory])

  // subcat selection
  useEffect(() => {
    if (fetched) {
      const currActive = document.getElementById(`sslide-${prevSubcategory}`);
      const newActive = document.getElementById(`sslide-${activeSubcategory}`);

      currActive.style.background = '#FFFFFF'
      newActive.style.background = 'linear-gradient(180deg, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 100%), #009CDB'

      setActiveBalloon(0);
      console.log('activecat', activeCategory);
      console.log('activeSubcategory', activeSubcategory);
      fetchBalloons(categories[activeCategory].categories[activeSubcategory].id);
      setPrevSubcategory(activeSubcategory);
    }
  }, [activeSubcategory])

  // balloon selection
  useEffect( () => {
    if (fetched) {
      const currActive = document.getElementById(`active-product-${prevBalloon}`);
      const newActive = document.getElementById(`active-product-${activeBalloon}`)
      
      currActive.lastChild.style.display = "none";
      currActive.style.border = "1px solid #E4E4E4"

      newActive.lastChild.style.display = "block";
      newActive.style.border = "2px solid #009CDB"

      setPrevBalloon(activeBalloon);
    }
  }, [activeBalloon]);


  const fetchCategories = async () => {
    const response = await axios.get(
      `https://peaceful-stream-16724.herokuapp.com/https://test-web.partycity.com/s/Sites-partycity_us2-Site/dw/shop/v20_4/categories/balloons?client_id=f75472a2-de04-4b73-80e1-32a261e83281&levels=2`
    );

    const cats = response.data.categories.slice(0, 3);   

    // hardcode navbar for foil/latex
    cats[1] = cats[0].categories[0];
    cats[1].categories = [];
    cats[2] = cats[0].categories[1];
    cats[2].categories = [];

     // filter subcats
    const goodCats = cats[0].categories.filter(subcat => {
      return (subcat.c_kioskTemplate || subcat.c_kioskTemplate == 'true')
    })
    cats[0].categories = goodCats;

    // hardcode sample balloons
    cats[0].categories.unshift({ name: "Sample Balloons", id: "sample-balloons" })

    setCategories(cats);
    fetchBalloons('sample-balloons')
    setFetched(true);
  }

  const fetchBalloons = async (cgid) => {
    if (cgid === 'sample-balloons') {
      const sample = [{ product_name: "Sample Balloon", price: "9.99", represented_product: { id: 47094 }}, { product_name: "Sample Balloon", price: "9.99", represented_product: { id: 863280} }, { product_name: "Sample Balloon", price: "9.99", represented_product: { id: 660902} }, { product_name: "Sample Balloon", price: "9.99", represented_product: { id: 662678} }, { product_name: "Sample Balloon", price: "9.99", represented_product: { id: 686233} }, { product_name: "Sample Balloon", price: "9.99", represented_product: { id: 740789} }, { product_name: "Sample Balloon", price: "9.99", represented_product: { id: 777434} }, { product_name: "Sample Balloon", price: "9.99", represented_product: { id: 794372} }, { product_name: "Sample Balloon", price: "9.99", represented_product: { id: 846715} }]

      setBalloons(sample);
    } else {
      const response = await axios.get(
        `https://peaceful-stream-16724.herokuapp.com/https://test-web.partycity.com/s/Sites-partycity_us2-Site/dw/shop/v20_4/product_search?client_id=f75472a2-de04-4b73-80e1-32a261e83281&count=200&refine_1=cgid=${cgid}&refine_2=c_kioskOnly=true&expand=images, prices`
      );

      setBalloons(response.data.hits);
    }
  }

  // const addBalloon = async (balloon, activeCategory) => {
  //   // await setSubtotal(subtotal + Number(balloon.price));
  //   await addToBasket(balloon.represented_product.id, activeCategory);
  //   await window.CloningBalloon(`${balloon.represented_product.id}`);
  // }

  // const addToBasket = async (productID, activeCategory) => {
  //   let item = [{
  //     "product_id": productID.toString(),
  //     "quantity": 1,
  //     // "c_category": !!activeCategory && activeCategory.name
  //   }];

  //   try {
  //     const token = await localStorage.ACCESS_TOKEN_KEY;
  //     const basketID = await localStorage.BASKET_ID_KEY;

  //     const basket = await addProductToBasket({ token, basketID, item });
  //     const { data } = basket;
  //     const { product_items, product_sub_total, order_total, tax_total } = data;

  //     const updatedCart = {
  //       product_items,
  //       product_sub_total,
  //       order_total,
  //       tax_total,
  //     };

  //     await updateCart(updatedCart);
  //   } catch (error) {
  //     console.log('Add Item to Basket Error:', error);
  //   }
  // }

  const handleCheckout = async () => {
    if (!subtotal) return;
    localStorage.CART = await JSON.stringify(cart);
    history.push(`/checkout`);
  }

  // console.log('herman', herman);

  return (
    <div className="BB">
      <div className="navbar">
        <Navbar
          categories={categories}
          activeCategory={activeCategory}
          handleEditPress={() => handleEditPress()}
          setActiveCategory={setActiveCategory}
          />
      </div>
      <div className="bottom-drawer">
        <div className="subcat-carousel">
          <SubcatCarousel
            key={activeCategory}
            subcategories={categories[activeCategory]}
            setActiveSubcategory={setActiveSubcategory}
          />
        </div>
        <div className="product-carousel">
          <ProductCarousel 
            key={activeSubcategory}
            balloons={balloons} 
            setActiveBalloon={setActiveBalloon}
            subtotal={subtotal}
            setSubtotal={setSubtotal}
            updateCart={updateCart}
          />
        </div>
        <div className="nav-buttons">
          <div className="back-button">
            <img src={Arrow} className="back-image" />
            <div className="back-title">BACK</div>
          </div>
          <div className="continue-button" onClick={() => handleCheckout()}>
            <div className="continue-price">${subtotal.toFixed(2)}</div>
            <div className="continue-title">CONTINUE</div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Edit;


