import React from 'react';
import { CarouselProvider, Slider, Slide, Image } from 'pure-react-carousel';
import { addProductToBasket } from '../utils/Service'
import 'bootstrap/dist/css/bootstrap.min.css';
import 'pure-react-carousel/dist/react-carousel.es.css';
import "./productCarousel.css"

const ProductCarousel = ({ 
  balloons, 
  setActiveBalloon, 
  activeCategory,
  setSubtotal, 
  subtotal,  
  updateCart,
  // setEnableCheckout
}) => {

  const addBalloon = async (balloon, activeCategory) => {
    await setSubtotal(subtotal + Number(balloon.price));
    await addToBasket(balloon.represented_product.id, activeCategory);
    await window.CloningBalloon(`${balloon.represented_product.id}`);
  }

  const addToBasket = async (productID, activeCategory) => {
    let item = [{
      "product_id": productID.toString(),
      "quantity": 1,
      // "c_category": !!activeCategory && activeCategory.name
    }];

    try {
      const token = await localStorage.ACCESS_TOKEN_KEY;
      const basketID = await localStorage.BASKET_ID_KEY;

      const basket = await addProductToBasket({ token, basketID, item });
      const { data } = basket;
      const { product_items, product_sub_total, order_total, tax_total } = data;

      const updatedCart = {
        product_items,
        product_sub_total,
        order_total,
        tax_total,
      };

      await updateCart(updatedCart);
    } catch (error) {
      console.log('Add Item to Basket Error:', error);
    }
  }

  // // console.log('ac', !!activeCategory && activeCategory.name)
  // const addBalloon = async (balloon) => {
  //   await window.CloningBalloon(`${balloon.represented_product.id}`);
  //   await addToBasket(balloon.represented_product.id, activeCategory);
  //   await setSubtotal(JSON.parse(localStorage.CART).product_sub_total);
  // }

  // const addToBasket = async (productID, activeCategory) => {
  //   let item = [{ 
  //     "product_id": productID.toString(), 
  //     "quantity": 1,
  //     // "c_category": !!activeCategory && activeCategory.name
  //   }];

  //   try {
  //     const token = await localStorage.ACCESS_TOKEN_KEY;
  //     const basketID = await localStorage.BASKET_ID_KEY;
      
  //     const basket = await addProductToBasket({ token, basketID, item });
  //     const { data } = basket;
  //     const {product_items, product_sub_total, order_total, tax_total } = data;

  //     const cart = {
  //       product_items,
  //       product_sub_total,
  //       order_total,
  //       tax_total,
  //     };

  //     //need to add category
  //     localStorage.CART = await JSON.stringify(cart);
  //     } catch (error) {
  //     console.log('Add Item to Basket Error:', error);
  //   }
  // }

  return (
      <div className="productCarousel">
      <CarouselProvider
        className="carousel-provider"
        isIntrinsicHeight={true}
        totalSlides={!!balloons && balloons.length}
        visibleSlides={2.2}
        currentSlide={0}
      >
        <Slider className="slider" classNameTray="tray" classNameTrayWrap="tray-wrap">
          {!!balloons && balloons.map((balloon, idx) => (
            <Slide index={idx} key={idx} className='product-slide'>
              <div 
                className="slide-container" 
                id={`active-product-${idx}`}
                onClick={() => setActiveBalloon(idx)}
              >
                <div className="image">
                  <img className="p-image" src={`https://partycity.scene7.com/is/image/PartyCity/_sq_?$_500x500_$&$product=PartyCity/${balloon.represented_product.id}`} />
                </div>
                <div className="name">
                  {balloon.product_name.toUpperCase()}
                </div>
                <div className="price">
                  ${balloon.price}
                </div>
                <div className="add" id={`add-${idx}`}>
                  <button className="button" 
                    onClick={() => {
                      addBalloon(balloon, activeCategory);
                    }}>
                    ADD
                  </button>
                </div>  
              </div>
            </Slide>
          ))}
        </Slider>
      </CarouselProvider>
    </div>
  );
}

export default ProductCarousel;

