import React, { useEffect, useState } from 'react';
import Arrow from '../components/icons/Back.png'
import Inflation from '../components/icons/Inflation.png'
import Dropdown from '../components/icons/Dropdown.png'
import ArrowUp from '../components/icons/ArrowUp.png'
import CheckoutItem from '../components/checkoutItem'
import { withRouter } from 'react-router-dom';
import "./Checkout.css"

const Checkout = ({ history }) => {
  const [subtotal, setSubtotal] = useState(0);
  const [items, setItems] = useState([]);

  console.log('hello')

  useEffect(async () => {
    const cart = await JSON.parse(localStorage.CART);
    await setSubtotal(cart.product_sub_total)
    await setItems(cart.product_items)
  }, []);


  return (
    <div className="co-page-container">

      <div className="co-page">

        <div className="co-nav-buttons">
          <button className="co-back" onClick={() => history.goBack()}>
            <img src={Arrow} className="back-image" />
            <div className="co-back-title">BACK</div>
          </button>
          <button className="co-keep-shopping" onClick={() => history.goBack()}>
            <div className="co-back-title">KEEP SHOPPING</div>
          </button>
        </div>

        <div className="co-your-cart">
          YOUR CART
        </div>

        <div className="co-cartview-buttons">
          <div className="co-by-scene">
            <div className="co-cartview-title">BY SCENE</div>
          </div>
          <div className="co-itemized">
            <div className="co-cartview-title">ITEMIZED LIST</div>
          </div>
        </div>

        <div className="co-bouquet-summary">
          <div className="co-bouquet-title">
            <div className="co-bouquet-title-line">LARGE</div>
            <div className="co-bouquet-title-line">BOUQUET</div>
          </div>
          <div className="co-bouquet-subtotal">
            <div className="co-bouquet-subtotal-title">SUBTOTAL</div>
            <div className="co-bouquet-subtotal-price">${subtotal}</div>
          </div>
        </div>

        <div className="co-inflation-service">
          <div className="co-inflation-heading">
              <div className="co-inflation-title">
              <img src={Inflation} className="co-inflation-icon" />&nbsp;Inflation Services
              </div>
            <div className="co-inflation-price">$3.49</div>
          </div>

          <div className="co-inflation-option">
            <div className="co-inflation-text">This&nbsp;</div>
            <div className="co-inflation-dropdown"> 
              will&nbsp;
              <div className="co-dropdown-icon">
                <div><img src={Dropdown} /></div>
              </div>
            </div>
            <div className="co-inflation-text">&nbsp;be inflated for you.</div>
          </div> 
        </div>

        <div className="co-detail-section">
          <div>DETAILS&nbsp;</div>
          <img src={ArrowUp} className="back-image" />
        </div>

        <div className="co-items">
          {items.map((item, idx) => (
            <CheckoutItem 
              key={idx}
              item_text={item.item_text}
              price={item.price}
              quantity={item.quantity}
              product_id={item.product_id}
            />
          ))}
        </div>

        <div className="co-checkout-button">
          <div className="co-checkout-button-price">${subtotal}</div>
          <div className="co-checkout-button-title">CHECK OUT</div>
        </div>
        
      </div>
    </div>
  )
}

export default withRouter(Checkout);